._3ab-k {
  border: 1px solid black;
}

._3ab-k:focus {
  outline: 1px solid blue;
}

._3ab-k > span {
  display: inline-block;
  padding: 2px 8px;
  margin: 4px 2px;
  background-color: #ccc;
  border-radius: 4px;
}

._3ab-k > span a {
  cursor: pointer;
}

._3ab-k input {
  padding: 2px 8px;
  font-size: 1em;
  border: none;
  height: 100%;
  background-color: transparent;
}

._3ab-k input:focus {
  border: none;
  outline: none;
}
